:root
    --fg: #ffffffe1
    --bg: #271e49
    --bg-error: #561010

*
    color: var(--fg)
    box-sizing: border-box

html
    font-family: system-ui, -apple-system, BlinkMacSystemFont, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif
    scroll-behavior: smooth
    overflow: hidden

div.wrapper
    height: auto
    max-width: 700px
    transform: translateY(-50%)

h1
    font-weight: 600

body
    margin: 0
    height: 100vh
    background: var(--bg)
    display: flex
    justify-content: center
    align-items: center
    line-height: 1.5
    position: relative


body.error
    background: var(--bg-error)

footer
    font-weight: 300
    padding-top: 0.5rem
    opacity: 0.5
    position: absolute
    bottom: 1rem
    left: 1rem
    width: 100%

code
    font-family: monospace
    font-size: 1rem
    background: #00000033
    box-shadow: 0 0 10px #00000036
    padding: 0.2rem 0.4rem
    border-radius: 0.2rem

    word-break: break-all
    word-wrap: break-word

